import { actionTypes } from "./";
import axios from "axios";
import { fromJS } from "immutable";

export const searchFocus = () => ({
	type: actionTypes.SEARCH_FOCUS
})

export const searchBlur = () => ({
	type: actionTypes.SEARCH_BLUR
})

export const mouseEnter = () => ({
	type: actionTypes.MOUSE_ENTER
})

export const mouseLeave = () => ({
	type: actionTypes.MOUSE_LEAVE
})

export const removeSpin = () => ({
	type: actionTypes.REMOVE_SPIN
})

// Return the current page
export const changePage = (page, spin) => ({
	type: actionTypes.CHANGE_PAGE,
	page,
	spin
})

// Use redux thunk to create async ajax call inside actionCreator
export const getList = () => {
	return (dispatch) => {
		axios.get("/api/headerList.json").then((res) => {
			dispatch(changeList(res.data.data));
		}).catch(() => {
			console.log("Failed");
		})
	}
}

// 10 items on a page
const itemsPerPage = 10;
// Convert data into immutable and get the total page count
const changeList = (data) => ({
	type: actionTypes.CHANGE_LIST,
	data: fromJS(data),
	totalPage: Math.ceil(data.length / itemsPerPage)
})
