import React, { PureComponent } from "react";
import { Redirect } from "react-router-dom";
import { connect } from "react-redux";
import { LoginWrapper, LoginBox, Input, Btn } from "./style";
import { actionCreator } from "./store";

class Login extends PureComponent{
  render(){
    const { loginStatus } = this.props;
    if(!loginStatus){
      return (
        <LoginWrapper>
          <LoginBox>
            <Input placeholder = "Username"  ref = {(input) => {this.account = input}}/>
            <Input placeholder = "Password" type = "password" ref = {(input) => {this.password = input}}/>
            <Btn onClick = {() => this.props.login(this.account, this.password)}>Login</Btn>
          </LoginBox>
        </LoginWrapper>
      );
    }else{
      return <Redirect to = "/" />
    }
  }
} 

const mapStateToProps = (state) => ({
  loginStatus: state.getIn(["login", "login"])
})  

const mapDispatchToProps = (dispatch) => ({
  login(account, password){
    dispatch(actionCreator.login(account.value, password.value));
  }
})  

export default connect(mapStateToProps, mapDispatchToProps)(Login);