import React, { PureComponent } from "react";
import { connect } from "react-redux";
import { HomeWrapper, HomeLeft, HomeRight } from "./style";
import Topic from "./components/Topic";
import List from "./components/List";
import Recommend from "./components/Recommend";
import Writer from "./components/Writer";
import Commercial from "./components/Commercial";
import { actionCreator } from "./store";
import { BackTop } from "./style";
import { faChevronUp, faChevronLeft, faChevronRight } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

class Home extends PureComponent{
	render(){
    const { showScroll, handleMouseEnter, handleMouseLeave, index } = this.props;
    const setClassName = `banner-img banner-img-${index}`;
		return(
			<HomeWrapper>
				<HomeLeft>
        {/*Switch between different banner images*/}
          <div onMouseEnter = {handleMouseEnter} onMouseLeave = {handleMouseLeave} className = {setClassName}>
            {this.getArrows()}
          </div>
					<Topic />
					<List />
				</HomeLeft>
				<HomeRight>
					<Recommend />
					<Commercial />
					<Writer />
				</HomeRight>
        {showScroll ? <BackTop onClick = {this.handleScrollTop}><FontAwesomeIcon icon = {faChevronUp} /></BackTop> : null}
      </HomeWrapper>        
		);
	}

  // Set up the arrows on the banner image for sliding
  getArrows(){
    const { arrowShow, handleLeftClick, handleRightClick, index } = this.props;
    if(arrowShow){
      return(
        <div>
          <FontAwesomeIcon onClick = {() => handleLeftClick(index)} className = "leftArrow" icon = {faChevronLeft} />
          <FontAwesomeIcon onClick = {() => handleRightClick(index)} className = "rightArrow" icon = {faChevronRight} />
        </div>
      );
    }else{
      return null;
    }
  }

	componentDidMount(){
		this.props.changeHomeData();
    this.bindEvents();
	}

  // Remove event listener when the component unmounts
  componentWillUnmount(){
    window.removeEventListener("scroll", this.props.changeScrollTopShow);
  }

  bindEvents(){
    window.addEventListener("scroll", this.props.changeScrollTopShow);
  }

  handleScrollTop(){
    window.scrollTo(0, 0);
  }
}

const mapStateToProps = (state) => ({
  showScroll: state.getIn(["home", "showScroll"]),
  arrowShow: state.getIn(["home", "arrowShow"]),
  index: state.getIn(["home", "index"])
});

const mapDispatchToProps = (dispatch) => ({
	changeHomeData(){
		dispatch(actionCreator.getHomeInfo());
	},

  handleMouseEnter(){
    dispatch(actionCreator.arrowDisplay());
  },

  handleMouseLeave(){
    dispatch(actionCreator.arrowHide());
  },

  handleLeftClick(index){
    dispatch(actionCreator.pageBack(index));
  },

  handleRightClick(index){
    dispatch(actionCreator.pageForward(index));
  },

  changeScrollTopShow(){
    if (document.documentElement.scrollTop > 200) {
      dispatch(actionCreator.toggleTopShow(true));
    }else{
      dispatch(actionCreator.toggleTopShow(false));
    }
  }
});

export default connect(mapStateToProps, mapDispatchToProps)(Home);