import React, { PureComponent } from "react";
import { WriterWrapper, WriterHeader, WriterItem, BtnWrapper, WriterNextPage } from "../style";
import { faSync, faPlus, faChevronRight } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { connect } from "react-redux";
import { actionCreator } from "../store";

class Writer extends PureComponent{
	constructor(props){
		super(props);
		this.getSpinIcon = this.getSpinIcon.bind(this);
		this.removeSpinIcon = this.removeSpinIcon.bind(this);
	}

	render(){
		// Destructuring
		const { handlePageChange, page, totalPage, spin } = this.props;
		return(
			<WriterWrapper>
				<WriterHeader>
					<div className = "recommendation">Recommendation</div>
					<div className = "newOnes" onClick = {() => handlePageChange(page, totalPage, spin)}>
						{/*Get the spinning icon*/}
						{this.getSpinIcon(spin)}
						<WriterNextPage>Get different ones</WriterNextPage>
						{/*Stop the icon from spinning*/}
						{this.removeSpinIcon(spin)}
					</div>
				</WriterHeader>

				{/*Render the page*/}
				{this.getListArea()}

				<BtnWrapper>
					<button className = "writerBtn">See all <FontAwesomeIcon icon = {faChevronRight}/></button>
				</BtnWrapper>
			</WriterWrapper>
		);
	}

	getListArea(){
    // Show 5 items per page
		const itemsPerPage = 5;
		// Destructuring props
		const { list, page } = this.props;
		// Turn immutable data back to JS object
		const newList = list.toJS();
		// The array which contains the 10 items to be displayed
		const pageList = [];
		// Do nothing if there's no pageList
		if(newList.length){
			// Set pageList according to the current page
			for (let i = (page - 1) * itemsPerPage; i < page * itemsPerPage; i++) {
				pageList.push(newList[i]);
			}
		}

		return(
			pageList.map((item) => {
				return(
					<WriterItem key = {item.id}>
						<img alt = "" className = "writer-pic" src = {item.imgUrl}/>
						<div className = "writerDesc">
							<p className = "WriterText">{item.name}</p>
							<p className = "WriterText grey">{item.number}</p>
						</div>
						<div className = "plusIcon">
							<FontAwesomeIcon icon = {faPlus}/> Go
						</div>
					</WriterItem>
				);
			})
		);
	}

	// Make the icon spin depends on the input
	getSpinIcon(spin){
		if(spin){
			return (<FontAwesomeIcon icon = {faSync} spin/>);
		}else{
			return (<FontAwesomeIcon icon = {faSync}/>);
		}
	}

	// Remove the spin effect after 0.5s
	removeSpinIcon(spin){
		if(spin){
			setTimeout(this.props.RemoveSpinEffect, 500);
		}
	}
}

const mapStateToProps = (state) => {
	return{
		list: state.getIn(["home", "writerList"]),
		spin : state.getIn(["home", "spin"]),
		page: state.getIn(["home", "page"]),
		totalPage : state.getIn(["home", "totalPage"])
	}
}

// Map the methods that require access to data to props
const mapDispatchToProps = (dispatch) => {
	return{
		// Control the page count
		handlePageChange(page, totalPage, spin, list){
			if(page < totalPage){
				dispatch(actionCreator.changePage(page + 1));
			}else{
				dispatch(actionCreator.changePage(1));
			}
		},

		RemoveSpinEffect(){
			dispatch(actionCreator.removeSpin());
		}
	}
}

export default connect(mapStateToProps, mapDispatchToProps)(Writer);