import { actionTypes } from "./";
import axios from "axios";

export const removeSpin = () => ({
	type: actionTypes.REMOVE_SPIN
});

// Return the current page
export const changePage = (page, spin) => ({
	type: actionTypes.CHANGE_PAGE,
	page,
	spin
});

export const toggleTopShow = (show) => ({
	type: actionTypes.TOGGLE_SCROLL_TOP,
	show
});

export const arrowDisplay = () => ({
  type: actionTypes.ARROW_DISPLAY
});

export const arrowHide = () => ({
  type: actionTypes.ARROW_HIDE
});

export const pageBack = (index) => ({
  type: actionTypes.PAGE_BACK,
  page: index - 1
});

export const pageForward = (index) => ({
  type: actionTypes.PAGE_FORWARD,
  page: index + 1
});

export const getHomeInfo = () => {
	return (dispatch) => {
		axios.get("/api/homeList.json").then((res) => {
			const result = res.data.data;
			dispatch(changeHomeData(result));
		}).catch(() => {
			console.log("Failed");
		})
	}
}

export const getMoreList = (page) => {
	return (dispatch) => {
		axios.get("/api/MoreHomeList.json?page=" + page).then((res) => {
			const result = res.data.data;
			dispatch(addHomeList(result, page + 1));
		}).catch(() => {
			console.log("failed");
		})
	}
}

// Show 5 items per page
const itemsPerPage = 5;
const changeHomeData = (result) => ({
	type: actionTypes.CHANGE_HOME_DATA,
	topicList: result.topicList,
	articleList: result.articleList,
	recommendList: result.recommendList,
	writerList: result.writerList,
	totalPage: Math.ceil(result.writerList.length / itemsPerPage),
  imageList: result.imageList
});

const addHomeList = (result, nextPage) => ({
	type: actionTypes.ADD_ARTICLE_LIST,
	list: result.articleList,
	nextPage
});
