import { fromJS } from "immutable";
import { actionTypes } from "./";

// There are 5 images in total to slide
const imageNumber = 6;
const defaultState = fromJS({
	spin: false,
	page: 1,
	totalPage: 1,
	articlePage: 1,
	showScroll: false,
  arrowShow: true,
	topicList : [],
	articleList: [],
	recommendList: [],
	writerList: [],
  index: 0
})

const changePage = (state, action) => {
	return state.merge({
		page: action.page,
		spin: true
	})
};

const backPage = (state, action) => {
  if (action.page < 0){
    // Reset the image to be the last image
    return state.set("index", imageNumber - 1);
  }
  else{
    // Show the previous page
    return state.set("index", action.page);
  }
};

const forwardPage = (state, action) => {
  if (action.page > imageNumber - 1){
    // Reset the image to be the first image
    return state.set("index", 0);
  }
  else{
    // Show the next page
    return state.set("index", action.page);
  }
};

const changeHomeData = (state, action) => {
	return state.merge({
		// Change data to immutable
		topicList : fromJS(action.topicList),
		articleList: fromJS(action.articleList),
		recommendList: fromJS(action.recommendList),
		writerList: fromJS(action.writerList),
		totalPage: fromJS(action.totalPage)
	})
};

const addArticleList = (state, action) => {
	return state.merge({
		"articleList": state.get("articleList").concat(fromJS(action.list)),
		"articlePage": action.nextPage
	})
};

export default (state = defaultState, action) => {
	switch(action.type){
		case actionTypes.CHANGE_PAGE:
			// Change the current page and add spinning to the icon
			return changePage(state, action);
		case actionTypes.REMOVE_SPIN:
		// Remove the spinning effect
			return state.set("spin", false);
		case actionTypes.CHANGE_HOME_DATA:
		// Retrieve the default data
			return changeHomeData(state, action);
		case actionTypes.ADD_ARTICLE_LIST:
			// Add more data to article list
			return addArticleList(state, action);
		case actionTypes.TOGGLE_SCROLL_TOP:
			// Set the back-to-top button status
			return state.set("showScroll", action.show);
    case actionTypes.ARROW_DISPLAY:
      // Show the arrows
      return state.set("arrowShow", true);
    case actionTypes.ARROW_HIDE:
      // Hide the arrows
      return state.set("arrowShow", false);
    case actionTypes.PAGE_BACK:
      // Get the previous image
      return backPage(state, action);
    case actionTypes.PAGE_FORWARD:
      // Get the next image
      return forwardPage(state, action);
		default:
			return state;
	}
}