import React, { PureComponent } from "react";
import { Redirect } from "react-router-dom";
import { connect } from "react-redux";
import { WriteArticlesWrapper, NewArticleWrapper, ToolBar, ToolItem, ToolSubmitBtn, MoreItems } from "./style";
import { faRedo, faFont, faBold, faItalic, faUnderline, faAlignLeft, faAlignCenter, faAlignRight, faIndent, faListUl, faListOl, faCaretDown } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

class WriteArticles extends PureComponent{
  render(){
    const { loginStatus } = this.props;
    if(loginStatus){
      return(
        <WriteArticlesWrapper>
          <div className = "newArticleText">Post a new article:</div>
          <NewArticleWrapper />
          <ToolBar>
            <ToolItem><FontAwesomeIcon className = "toolIcon" icon = {faRedo} /></ToolItem>
            <ToolItem><FontAwesomeIcon className = "toolIcon" icon = {faFont} /></ToolItem>
            <ToolItem><FontAwesomeIcon className = "toolIcon" icon = {faBold} /></ToolItem>
            <ToolItem><FontAwesomeIcon className = "toolIcon" icon = {faItalic} /></ToolItem>
            <ToolItem><FontAwesomeIcon className = "toolIcon" icon = {faUnderline} /></ToolItem>
            <ToolItem><FontAwesomeIcon className = "toolIcon" icon = {faAlignLeft} /></ToolItem>
            <ToolItem><FontAwesomeIcon className = "toolIcon" icon = {faAlignCenter} /></ToolItem>
            <ToolItem><FontAwesomeIcon className = "toolIcon" icon = {faAlignRight} /></ToolItem>
            <ToolItem><FontAwesomeIcon className = "toolIcon" icon = {faIndent} /></ToolItem>
            <ToolItem><FontAwesomeIcon className = "toolIcon" icon = {faListUl} /></ToolItem>
            <ToolItem><FontAwesomeIcon className = "toolIcon" icon = {faListOl} /></ToolItem>
            <MoreItems><FontAwesomeIcon className = "toolIcon" icon = {faCaretDown} /></MoreItems>
          </ToolBar>
          <ToolSubmitBtn>Submit</ToolSubmitBtn>
        </WriteArticlesWrapper>
      );
    }else{
      return <Redirect to = "/login" />
    }
  }
} 

const mapStateToProps = (state) => ({
  loginStatus: state.getIn(["login", "login"])
})  

export default connect(mapStateToProps, null)(WriteArticles);