import styled from "styled-components";
import pastry from "../../Statics/pastry.jpg";
import cake from "../../Statics/cake.jpg";
import apples from "../../Statics/apples.jpg";
import dim_sum from "../../Statics/dim_sum.jpg";
import bread from "../../Statics/bread.jpg";
import pumpkins from "../../Statics/pumpkins.jpg";
import sunflowers from "../../Statics/sunflowers.jpg";
import vegetables from "../../Statics/vegetables.jpg";

export const HomeWrapper = styled.div`
	width: 1500px;
	margin: 0 auto;
	overflow: hidden;
`;

export const HomeLeft = styled.div`
	width: 1000px;
	padding-top: 15px;
	float: left;
	.banner-img{
		width: 100%;
		height: 500px;
    transition: background-image 0.5s ease-in-out;
    border-radius: 10px;
    cursor: pointer;
	}
  .banner-img-0{
    background: url(${dim_sum});
    background-size: cover;
    background-position: center center;
  }
  .banner-img-1{
    background: url(${bread});
    background-size: cover;
    background-position: center center;
  }
  .banner-img-2{
    background: url(${apples});
    background-size: cover;
    background-position: center center;
  }
  .banner-img-3{
    background: url(${pumpkins});
    background-size: cover;
    background-position: center center;
  }
  .banner-img-4{
    background: url(${sunflowers});
    background-size: cover;
    background-position: center center;
  }
  .banner-img-5{
    background: url(${vegetables});
    background-size: cover;
    background-position: center center;
  }
  .leftArrow{
    font-size: 100px;
    float: left;
    height: 500px;
    color: rgba(255, 255, 255, 0.4);
  }
  .rightArrow{
    font-size: 100px;
    float: right;
    height: 500px;
    color: rgba(255, 255, 255, 0.4);
  }
`;

export const HomeRight = styled.div`
	width: 500px;
	float: right;
`;

export const TopicWrapper = styled.div`
	padding: 20px 0 10px 0;
	overflow: hidden;
	margin-left: -18px;
	border-bottom: 1px solid #dcdcdc;
`;

export const TopicItem = styled.div`
	float: left; 
	background: #f7f7f7;
	margin-left: 18px;
	margin-bottom: 18px;
	height: 32px;
	line-height: 32px;
	font-size: 18px;
	border: 1px solid #dcdcdc;
	border-radius: 4px;
	color: black;
	padding-right: 10px;
  cursor: pointer;
	.topic-pic{
		width: 32px;
		height: 32px;
		display: block;
		float: left;
		margin-right: 10px;
	}
`;

export const TopicMore = styled.div`
	padding: 5px 0;
	float: left;
	overflow: hidden;
	margin-left: 28px;
	color: grey;
	font-size: 20px;
	cursor: pointer;
`;

export const ListItem = styled.div`
	padding: 20px 0;
	border-bottom: 1px solid #dcdcdc;
	overflow: hidden;
	.list-pic{
		width: 148px;
		height: 98px;
		display: block;
		float: right;
		border-radius: 10px;
		margin-top: 5px;
	}
`;

export const ListInfo = styled.div`
	width: 500px;
	.title{
		margin-top: -10px;
		font-size: 20px;
		line-height: 27px;
		font-weight: bold;
		color: #333;
	}
	.desc{
		font-size: 18px;
		line-height: 24px;
		color: #999;
	}
`;

export const LoadMore = styled.div`
	width: 100%;
	height: 40px;
	line-height: 40px;
	text-align: center;
	background: #a5a5a5;
	border-radius: 20px;
	color: #fff;
	margin: 30px 0;
	cursor: pointer;
`;

export const OrangeIcon = styled.div`
	color: #ea6f5a;
	display: inline-block;
	margin-right: 20px;
	font-size: 12px;
`;

export const RegularIcon = styled.div`
	display: inline-block;
	margin-right: 20px;
	font-size: 12px;
`;

export const BottomIcons = styled.div`
	margin-bottom: -10px;
  cursor: pointer;
`;

export const RecommendWrapper = styled.div`
	margin: 15px 50px;
	margin-bottom: 0;
	width: 280px;
	cursor: pointer;
`;

export const RecommendItem = styled.div`
	width: 280px;
	height: 250px;
  margin: 10px 0;
	background: url(${pastry});
	background-size: cover;
  border-radius: 10px;
`;

export const RecommendItem1 = styled.div`
  width: 280px;
  height: 250px;
  margin: 10px 0;
  background: url(${cake});
  background-size: cover;
  border-radius: 10px;
`;

export const Download = styled.div`
	width: 278px;
	height: 80px;
	border: 1px solid #dcdcdc;
	border-radius: 8px;
	box-sizing: border-box;
	margin: 0 51px;
	margin-top: 10px;
	margin-bottom: 30px;
	cursor: pointer;
	.commercial-pic{
		width: 60px;
		height: 60px;
		margin: 8px 20px;
		margin-right: 0;
		float: left;
	}
`;

export const DownloadText = styled.div`
	width: 170px;
	height: 80px;
	float: right;
	margin: 12px 0;
	margin-right: 15px;
	margin-left: 10px;
	.text{
		margin: 5px 0;
	}
	.grey{
		color: #999;
	}
`;

export const CommercialWrapper = styled.div`
	width: 280px;
	height: 122px;
	border: 1px solid #dcdcdc;
	border-radius: 5px;
	box-sizing: border-box;
	margin: 30px 50px;
	margin-bottom: 15px;
	background: url("https://cdn2.jianshu.io/assets/javascripts/web/components/YoudaoAD/ad1-97702bbd807b2136f7ab1ecce6c4b663.png");
	background-size: contain;
	background-repeat: no-repeat;
	cursor: pointer;
`;

export const WriterWrapper = styled.div`
	width: 280px;
	border-radius: 3px;
	box-sizing: border-box;
	margin: 30px 50px;
	margin-top: 20px;
`;
 
export const WriterHeader = styled.div`
	overflow: hidden;
	.recommendation{
		float: left;
		color: #969696;
	}
	.newOnes{
		float: right;
		color: #969696;
		cursor: pointer;
	}
`;

export const WriterNextPage = styled.div`
	float: right;
	font-size: 18px;
	cursor: pointer;
	display: inline-block;
	margin-left: 5px;
`;

export const WriterItem = styled.div`
	margin: 15px 0;
	display: block;
	overflow: hidden;
	cursor: pointer;
	.writer-pic{
		width: 48px;
		height: 48px;
		border-radius: 24px;
		float: left;
	}
	.writerDesc{
		float: left;
		margin-left: 10px;
	}
	.WriterText{
		margin: 5px 0;
	}
	.grey{
		color: #969696;
		cursor: default;
	}
	.plusIcon{
		float: right;
		line-height: 48px;
		font-size: 18px;
		color: #42c02e;
	}
`;

export const BtnWrapper = styled.div`
	.writerBtn{
		width: 280px;
		padding: 10px;
		color: #787878;
		cursor: pointer;
		background: #f7f7f7;
		border: 1px solid #dcdcdc;
		border-radius: 4px;
		cursor: pointer;
	}
`;

export const BackTop = styled.div`
  position: fixed;
  right: 100px;
  bottom: 100px;
  width: 60px;
  height: 60px;
  line-height: 60px;
  text-align: center;
  border: 1px solid #ccc;
  border-radius: 50%;
  font-size: 20px;
  cursor: pointer;
`;