import React, { PureComponent } from "react";
// Using react-redux to connect the component to store
import { connect } from "react-redux";
// Using styled components
import { HeaderWrapper, Logo, Brand, Nav, NavItem, NavSearch, Addition, Button, SearchInfo, SearchInfoTitle, 
	SearchInfoSwitch, SearchInfoItem, SearchInfoList, SearchWrapper, SearchInfoText } from "./style"
// Using font awesome icons
import { faSearch, faFont, faFeatherAlt, faSync } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { CSSTransition } from "react-transition-group";
// Using CSSTransition
import { actionCreator } from "./store";
import { actionCreator as loginActionCreator} from "../../pages/login/store";
import { Link } from "react-router-dom";

class Header extends PureComponent{
	constructor(props){
		super(props);
		this.getListArea = this.getListArea.bind(this);
		this.getSpinIcon = this.getSpinIcon.bind(this);
		this.removeSpinIcon = this.removeSpinIcon.bind(this);
	}
	
	render(){
		// Destructuring props
		const { focus, handleInputFocus, handleInputBlur, list, login, logout } = this.props;
		return (
			<HeaderWrapper>
        <Link to = "/">
				  <Logo/>
          <Brand>Tasty Affairs</Brand>
        </Link>
				{/*Navbar*/}
				<Nav>
					<Link to = "/">
            <NavItem className = "left active">Home</NavItem>
          </Link>
					<NavItem className = "left">Download</NavItem>

          {
            login ? 
            <NavItem onClick = {logout} className = "right">Log out</NavItem> : 
            <Link to = "/login"><NavItem className = "right">Log in</NavItem></Link>
          }

					<NavItem className = "right"><FontAwesomeIcon icon = {faFont} /></NavItem>
					{/*Text box for search*/}
					<SearchWrapper 
						onFocus = {() => handleInputFocus(list)}
						onBlur = {handleInputBlur}
					>
						{/*Add animation on text box and search icon*/}
						<CSSTransition
							in={focus}
			        timeout={300}
			        classNames="slide"
						>
							<NavSearch className = {focus? "focused": ""}></NavSearch>
						</CSSTransition>
						<FontAwesomeIcon className = {focus? "focused icon": "icon"} icon = {faSearch} />
						{/*Render the search info box*/}
						{this.getListArea()}
					</SearchWrapper>
				</Nav>
        
		    {/*Buttons*/}
				<Addition>
          <Link to = "/writeArticles">
				    <Button className = "write"><FontAwesomeIcon icon = {faFeatherAlt} /> Write an article</Button>
					</Link>
          <Button className = "reg">Sign up</Button>
				</Addition>
			</HeaderWrapper>
		);	
	}

	// Search box drop down menu
	getListArea(){
		// Destructuring props
		const { focus, spin, list, page, totalPage, mouseIn, handleMouseEnter, handleMouseLeave, handlePageChange } = this.props;
		// Turn immutable data back to JS object
		const newList = list.toJS();
		// The array which contains the 10 items to be displayed
		const pageList = [];
    // Show 10 items on a page
    const itemsPerPage = 10;
		// Do nothing if there's no pageList
		if(newList.length){
			// Set pageList according to the current page
			for (let i = (page - 1) * itemsPerPage; i < page * itemsPerPage; i++) {
				pageList.push(<SearchInfoItem key = {newList[i]}>{newList[i]}</SearchInfoItem>)
			}
		}

		// Show search info whenever it's focused or pointed to by the cursor
		if(focus || mouseIn){
			return(
				<SearchInfo onMouseEnter = {handleMouseEnter} onMouseLeave = {handleMouseLeave}>
					<SearchInfoTitle>
						Top searches
						{/*Get the next page once clicked*/}
						<SearchInfoSwitch onClick = {() => handlePageChange(page, totalPage, spin)}> 
							{/*Get the spinning icon*/}
							{this.getSpinIcon(spin)}
							<SearchInfoText>Get different ones</SearchInfoText>
							{/*Stop the icon from spinning*/}
							{this.removeSpinIcon(spin)}
						</SearchInfoSwitch>
					</SearchInfoTitle>
					<SearchInfoList>
						{/*Show the current page*/}
						{pageList}
					</SearchInfoList>
				</SearchInfo>);
		}else{
			return null;
		}
	}

	// Make the icon spin depends on the input
	getSpinIcon(spin){
		if(spin){
			return (<FontAwesomeIcon icon = {faSync} spin/>);
		}else{
			return (<FontAwesomeIcon icon = {faSync}/>);
		}
	}

	// Remove the spin effect after 0.5s
	removeSpinIcon(spin){
		if(spin){
			setTimeout(this.props.RemoveSpinEffect, 500);
		}
	}
}

// Map the store state onto component's props
const mapStateToProps = (state) => {
	return{
		focus : state.getIn(["header", "focus"]),
		spin : state.getIn(["header", "spin"]),
		list: state.getIn(["header", "list"]),
		page: state.getIn(["header", "page"]),
		totalPage : state.getIn(["header", "totalPage"]),
		mouseIn: state.getIn(["header", "mouseIn"]),
    login: state.getIn(["login", "login"])
	}
}

// Map the methods that require access to data to props
const mapDispatchToProps = (dispatch) => ({
  handleInputFocus(list){
    // Only make ajax call once
    if(list.size === 0){dispatch(actionCreator.getList());}
    dispatch(actionCreator.searchFocus());
  },

  handleInputBlur(){
    dispatch(actionCreator.searchBlur());
  },

  handleMouseEnter(){
    dispatch(actionCreator.mouseEnter());
  },

  handleMouseLeave(){
    dispatch(actionCreator.mouseLeave());
  },

  // Control the page count
  handlePageChange(page, totalPage, spin){
    if(page < totalPage){
      dispatch(actionCreator.changePage(page + 1));
      console.log(page)
    }else{
      dispatch(actionCreator.changePage(1));
    }
  },

  RemoveSpinEffect(){
    dispatch(actionCreator.removeSpin());
  },

  logout(){
    dispatch(loginActionCreator.logout());
  }
})

export default connect(mapStateToProps, mapDispatchToProps)(Header);