import React, { PureComponent } from "react";
import { ListItem, ListInfo, BottomIcons, OrangeIcon, RegularIcon, LoadMore } from "../style";
import { faGem, faCommentAlt, faHeart } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { connect } from "react-redux";
import { actionCreator } from "../store";
import { Link } from "react-router-dom";

class List extends PureComponent{
	render(){
		// Destructuring
		const { list, getMoreList, page } = this.props;
		return(
			<div>
				{list.map((item, index) => {
					return(
						<ListItem key = {index}>
              <Link to = {"/detail/" + item.get("id")}>
						    <img alt = "" className = "list-pic" src = {item.get("imgUrl")} />
              </Link>
							<ListInfo>
                <Link to = {"/detail/" + item.get("id")}>
  								<h3 className = "title">{item.get("title")}</h3>
                </Link>
								<p className = "desc">{item.get("desc")}</p>
							</ListInfo>
							<BottomIcons>
								<OrangeIcon><FontAwesomeIcon icon = {faGem} /> {item.get("gem")}</OrangeIcon>
								<RegularIcon>{item.get("author")}</RegularIcon>
								<RegularIcon><FontAwesomeIcon icon = {faCommentAlt} /> {item.get("comment")}</RegularIcon>
								<RegularIcon><FontAwesomeIcon icon = {faHeart} /> {item.get("like")}</RegularIcon>
							</BottomIcons>
						</ListItem>
					)
				})}
        <LoadMore onClick = {() => getMoreList(page)}>Load More</LoadMore>
			</div>
		);
	}
}

const mapStateToProps = (state) => ({
	list: state.getIn(["home", "articleList"]),
	iconList: state.getIn(["home", "iconList"]),
  page: state.getIn(["home", "articlePage"])
})

const mapDispatchToProps = (dispatch) => ({
  getMoreList(page){
    dispatch(actionCreator.getMoreList(page));
  }
})

export default connect(mapStateToProps, mapDispatchToProps)(List);