import React, { PureComponent } from 'react';
import { Provider } from 'react-redux';
import { BrowserRouter, Route } from "react-router-dom";
import Home from "./pages/home";
import Detail_1 from "./pages/detail_1/loadable";
import Detail_2 from "./pages/detail_2/loadable";
import Detail_3 from "./pages/detail_3/loadable";
import Detail_4 from "./pages/detail_4/loadable";
import Detail_5 from "./pages/detail_5/loadable";
import Detail_6 from "./pages/detail_6/loadable";
import Detail_7 from "./pages/detail_7/loadable";
import Detail_8 from "./pages/detail_8/loadable";
import Detail_9 from "./pages/detail_9/loadable";
import Detail_10 from "./pages/detail_10/loadable";
import Detail_11 from "./pages/detail_11/loadable";
import Detail_12 from "./pages/detail_12/loadable";
import Detail_13 from "./pages/detail_13/loadable";
import Detail_14 from "./pages/detail_14/loadable";
import Detail_15 from "./pages/detail_15/loadable";
import Login from "./pages/login";
import WriteArticles from "./pages/writeArticles";
import Header from "./common/header";
import store from "./store";

class App extends PureComponent{
  render(){
    return (
      <Provider store = {store}>
        <BrowserRouter>
          <Header />
        	<Route path = "/" exact component = {Home}></Route>
          <Route path = "/login" exact component = {Login}></Route>
          <Route path = "/writeArticles" exact component = {WriteArticles}></Route>
        	<Route path = "/detail/1" exact component = {Detail_1}></Route>
          <Route path = "/detail/2" exact component = {Detail_2}></Route>
          <Route path = "/detail/3" exact component = {Detail_3}></Route>
          <Route path = "/detail/4" exact component = {Detail_4}></Route>
          <Route path = "/detail/5" exact component = {Detail_5}></Route>
          <Route path = "/detail/6" exact component = {Detail_6}></Route>
          <Route path = "/detail/7" exact component = {Detail_7}></Route>
          <Route path = "/detail/8" exact component = {Detail_8}></Route>
          <Route path = "/detail/9" exact component = {Detail_9}></Route>
          <Route path = "/detail/10" exact component = {Detail_10}></Route>
          <Route path = "/detail/11" exact component = {Detail_11}></Route>
          <Route path = "/detail/12" exact component = {Detail_12}></Route>
          <Route path = "/detail/13" exact component = {Detail_13}></Route>
          <Route path = "/detail/14" exact component = {Detail_14}></Route>
          <Route path = "/detail/15" exact component = {Detail_15}></Route>
          
        </BrowserRouter>
      </Provider>
    );
  }
}

export default App;
