import styled from "styled-components";

export const HeaderWrapper = styled.div`
	height: 56px;
	border-bottom: 1px #f0f0f0 solid;
  margin: 0;
`;

export const Logo = styled.div`
	position: absolute;
	left: 0;
	top: 5px;
	display: inline-block;
	margin: 0 5px;
	height: 50px;
	width: 60px;
	background: url("https://secure.gravatar.com/avatar/a55abfd08606afc70393f764bf5ce2bd?s=150&r=pg&d=mm");
	background-size: cover;
  border: 2px solid #ea6f5a;
  border-radius: 10px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.3)
`;

export const Brand = styled.div`
  position: absolute;
  left: 0;
  top: 5px;
  display: inline-block;
  margin-left: 72px;
  margin-top: 12px;
  font-size: 25px;
  height: 50px;
  width: 160px;
  color: #ea6f5a;
`;

export const Nav = styled.div`
	position: absolute;
	top: 5px;
	width: 1200px;
	margin-left: 200px;
	display: inline-block;
`;

export const NavItem = styled.div`
	line-height: 56px;
	padding: 0 30px;
	font-size: 17px;
	color: #333;
	&.left{
		float: left;
    cursor: pointer;
	}
	&.right{
		float: right;
		color: #969696;
    cursor: pointer;
	}
	&.active{
		color: #ea6f5a;
	}
`;

export const SearchWrapper = styled.div`
	position: relative;
	float: left;
  z-index: 1;
	.icon{
		position: absolute;
		right: 5px;
		bottom: 3px;
		width: 20px;
		line-height: 20px;
		padding: 8px 6px;
		border-radius: 16px;
		color: grey;
		text-align: center;
		&.focused{
			background: #777;
			color: #fff;
		}
	}
`;

export const NavSearch = styled.input.attrs({placeholder: "Search"})`
	&::placeholder{
		color: #999;
	}
	width: 160px;
	height: 38px;
	box-sizing: border-box;
	padding: 0 30px 0 20px;
	margin-top: 9px;
	margin-left: 20px;
	border: none;
	outline: none;
	border-radius: 19px;
	background: #eee;
	font-size: 14px;
	color: #666;
	&.focused{
		width: 240px;
	}
	&.slide-enter {
	  transition: all 0.3s ease-out;
	}
	&.slide-enter-active {
	  width: 240px;
	}
	&.slide-exit {
	  transition: all 0.3s ease-in;
	}
	&.slide-exit-active {
	  width: 160px;
	}
`;

export const SearchInfo = styled.div`
	position: absolute;
	left: -30px;
	top: 60px;
	width: 300px;
	padding: 0 20px;
	box-shadow: 0 0 8px rgba(0, 0, 0, 0.2);
	background: white;
`;

export const SearchInfoTitle = styled.div`
	margin-top: 20px;
	margin-bottom: 15px;
	line-height: 20px;
	font-size: 20px;
	color: #969696;
`;

export const SearchInfoSwitch = styled.span`
	float: right;
	font-size: 18px;
	cursor: pointer;
`;

export const SearchInfoText = styled.div`
	float: right;
	font-size: 18px;
	cursor: pointer;
	display: inline-block;
	margin-left: 5px;
`;

export const SearchInfoItem = styled.a`
	line-height: 20px;
	padding: 0 5px;
	font-size: 18px;
	border: 1px solid #ddd;
	color: #787878;
	border-radius: 3px;
	display: block;
	float: left;
	margin-right: 10px;
	margin-bottom: 15px;
  cursor: pointer;
`;

export const SearchInfoList = styled.div`
	overflow: hidden;
`;


export const Addition = styled.div`
	float: right;
	height: 56px;
	display: inline-block;
`;

export const Button = styled.div`
	float: right;
	width: 200px;
	text-align: center;
	text-decoration: none;
	margin: 4px 20px;
	line-height: 38px;
	border-radius: 19px;
	border: 1px solid #ec6149;
	font-size: 20px;
	&.reg{
		color: #ec6149;
	}
	&.write{
		color: #fff;
		background: #ec6149;
	}
`;
