import styled from "styled-components";

export const WriteArticlesWrapper = styled.div`
  margin: 0 auto;
  width: 800px;
  height: 500px;
  .newArticleText{
    font-size: 25px;
    line-height: 60px;
    font-weight: bold;
  }
`;

export const NewArticleWrapper = styled.textarea.attrs({placeholder: "Tell us about your recent dining experience..."})`
  width: 100%;
  height: 700px;
  box-sizing: border-box;
  font-size: 25px;
  text-align: top;
  line-height: 30px;
  border: 1px solid white;
  border-radius: 5px;
  box-shadow: 0 0 3px rgba(0, 0, 0, 0.5);
  padding: 10px;
`;

export const ToolBar = styled.div`
  position: relative;
  top: -54px;
  width: 100%;
  height: 50px;
  border: 1px solid grey;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
  box-sizing: border-box;
  border-radius: 5px;
  cursor: pointer
  .toolIcon{
    width: 25px;
    height: 25px;
    margin: 12.5px;
  }
`;

export const MoreItems = styled.div`
  float: right;
  border-left: 1px solid grey;
  width: 50px;
  height: 50px;
`;

export const ToolItem = styled.div`
  width: 50px;
  height: 50px;
  float: left;
  margin: 0 5px;

`;

export const ToolSubmitBtn = styled.div`
  width: 100%;
  height: 50px;
  line-height: 50px;
  font-size: 20px;
  font-weight: bold;
  color: white;
  text-align: center;
  border-radius: 50px;
  cursor: pointer;
  background: #ea6f5a;
  border: 1px solid #ea6f5a;
  box-sizing: border-box;
`;

