import React, { PureComponent } from "react";
import { Download, DownloadText } from "../style";
import { faChevronRight } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

class Commercial extends PureComponent{
	render(){
		return(
			<div>
				<Download>
					<img alt = "" className = "commercial-pic" src = "https://cdn2.jianshu.io/assets/web/download-index-side-qrcode-cb13fc9106a478795f8d10f9f632fccf.png" />
					<DownloadText>
						<p className = "text">Download Mobile App <FontAwesomeIcon icon = {faChevronRight} /></p>
						<p className = "text grey">Find New Content</p>
					</DownloadText>
				</Download>
				{/*<CommercialWrapper></CommercialWrapper>*/}
			</div>
		);
	}
}

export default Commercial;